.tippy-box {
	pointer-events: all;
	opacity: 1 !important;
	box-shadow: 0 0px 1px 0 rgba($text-color-dark, 0.1) !important;

	.tippy-content {
		padding: 15px 20px 20px;
		opacity: 1 !important;
		white-space: pre-line;

		.info-tip-title {
			display: block;
			text-transform: uppercase;
			font-size: 15px;
			padding-bottom: 5px;
		}

		br {
			display: block;
			margin-bottom: 8px !important;
			content: " ";
		}

		a {
			color: rgb(var(--dpit-uac-prim-bg)) !important;
			font-size: 16px;
			font-weight: 600;
		}
	}


	&[data-theme="material"] {
		font-weight: 400 !important;
		background-color: rgba(var(--dpit-uac-main-menu-bg), 1) !important;
		color: rgba(var(--dpit-uac-main-menu), 1) !important;
	}

	&[data-theme="translucent"] {
		font-weight: 400 !important;
		background-color: rgba(var(--dpit-uac-main-menu), 1) !important;
		color: rgba(var(--dpit-uac-main-menu-bg), 1) !important;

		a {
			color: rgba(var(--dpit-uac-main-menu-bg), 1) !important;
		}

		box-shadow: 0px 0px 3px 0 rgba($text-color-dark, 0.35) !important;
		//outline: 1px solid rgba(var(--dpit-uac-main-menu), 1) !important;
	}



	&[data-placement="top"] {
		&[data-theme="material"] {
			.tippy-arrow {
				&::before {
					border-top-color: rgba(var(--dpit-uac-main-menu-bg), 1) !important;
				}
			}
		}

		&[data-theme="translucent"] {
			.tippy-arrow {
				&::before {
					border-top-color: rgba(var(--dpit-uac-main-menu), 1) !important;
				}
			}
		}

	}

	&[data-placement="bottom"] {
		&[data-theme="material"] {
			.tippy-arrow {
				&::before {
					border-bottom-color: rgba(var(--dpit-uac-main-menu-bg), 1) !important;
				}
			}

		}

		&[data-theme="translucent"] {


			.tippy-arrow {
				&::before {
					border-bottom-color: rgba(var(--dpit-uac-main-menu), 1) !important;
				}
			}
		}

	}
}