.dpit-modal {
	@include z-index(6, true);
	border-radius: 2px;
	box-sizing: border-box;
	@include dpitBoxShadow;
	color: rgb(var(--dpit-uac-main-menu));
	background-color: rgba(var(--dpit-uac-main-menu-bg), 0.97);
	position: fixed;
	opacity: 0;
	pointer-events: none;
	@include transitionFaster(opacity, false, linear);

	&.dpit-active {
		opacity: 1;
		pointer-events: all;
	}

	&__backdrop {
		@include z-index(5, true);
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		position: fixed;
		background-color: rgb(var(--dpit-uac-main-menu));

		@include transitionFaster(opacity, false, linear);
		overflow: hidden;
		opacity: 0;
		pointer-events: none;

		&.dpit-active {
			opacity: 0.93;
			pointer-events: all;
		}
	}

	&__title {
		background: none !important;
		text-transform: uppercase;
		border-bottom: none;
		margin: 0;

		@include primaryFont(700, rgb(var(--dpit-uac-main-menu)), 1.4em, 1.3, normal);

		@include atXS {
			font-size: 1.3em;
		}
	}

	&__close {
		position: absolute;
		right: 20px;
		top: 10px;
		font-size: 30px;
		opacity: 0.8;
		transition: opacity $transition-time-fast ease-in-out;
		cursor: pointer;
		color: rgba(var(--dpit-uac-main-menu));

		&:hover {
			opacity: 1;
		}
	}

	&__buttons {
		display: flex;
		justify-content: space-between;

		&--abs {
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
		}

		input.button,
		button.button {
			padding: 2px 20px;
			font-size: 15px;
			text-transform: uppercase;
			line-height: 2.15384615;


			@include atXXXS {
				font-size: 13px;
				padding: 8px 12px;
			}

			&.button-primary {
				font-weight: 600;
			}

			&.disabled {
				opacity: 0.25;
				cursor: default;
				pointer-events: none;
			}
		}

		#dp_wp-link-update {


			.spinner {
				pointer-events: none;
			}

			.button {
				float: right;
			}
		}
	}

	input.button,
	button.button {

		border-color: rgb(var(--dpit-uac-sec-border)) !important;
		//background-color: rgb(var(--dpit-uac-menu-a-item-bg));
		color: rgb(var(--dpit-uac-sec)) !important;

		&.button-primary {
			background-color: rgb(var(--dpit-uac-prim-bg)) !important;
			color: rgb(var(--dpit-uac-prim)) !important;

			&:hover {
				background-color: rgb(var(--dpit-uac-prim-bg-hov)) !important;
			}
		}
	}
}