@import '../../../includes/assets/css/partials/admin-theme-colors';
@import '../../../includes/assets/css/partials/animations';
@import '../../../includes/assets/css/modules/dpit-modal';
@import '../../../includes/assets/css/partials/dpit-wp-link';
@import '../../../includes/assets/css/modules/dpit-mobile-view-popup';
@import './partials/dpit-wp-link-public';
@import '../../../includes/assets/css/modules/dpit-sup';
@import '../../../includes/assets/css/modules/dpit-info-icon';
@import '../../../includes/assets/css/modules/tippy-box';
@import '../../../includes/assets/css/modules/dpit-pro-only';
@import '../../../includes/assets/css/modules/dpit-mce-dialog';


div[data-tippy-root] {
	@include z-index(16, true);
}

html.dpit-mm {
	#wpadminbar {
		z-index: 0;
	}
}

:root.dpit-builder-mode {
	--dpit-shadow-color: 30, 30, 30;
	--dpit-shadow-alpha: 0.3;
	--secondary-font: 'Arial', Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

	&.dpit-pause {

		.dpit-disable-interaction,
		.dpit-tooltip,
		.dpit-overlay-container {
			pointer-events: none !important;
		}
	}

	.dpit-wrap {
		>.dpit-builder {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
		}

		sup {
			font-size: 75%;
			line-height: 0;
			position: relative;
			vertical-align: baseline;
			top: -0.5em;
		}

		.dpit-button {

			&--skip,
			&--done {
				cursor: not-allowed;
			}
		}

		&.dpit-is-sticky-theme {
			.dpit-tooltip {
				&--top {
					.dpit-button {
						&--skip {
							display: none;
						}
					}
				}
			}
		}
	}

	&.dpit-target-selecting {

		.dpit-tooltip,
		.dpit-hidden-keep-events {
			pointer-events: none !important;
		}

		.dpit-highlight {}
	}

	&.dpit-step-edit-mode {

		.dpit-target {
			cursor: not-allowed !important;

			&--click {
				cursor: not-allowed !important;
			}
		}

		.dpit-target {
			* {
				cursor: not-allowed !important;
			}


			&--click {
				cursor: not-allowed !important;

				* {
					cursor: not-allowed !important;
				}

			}
		}
	}

}