@import '../consts.scss';

.dpit-mce-dialog {
	padding: 20px;
	min-width: 600px;

	@include atS {
		min-width: unset;
		border-radius: 0;
	}

	@include atXXS {
		padding: 15px;
	}

	.wp-editor-tools {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		width: 100%;

		&::after {
			display: none;
		}
	}

	.wp-media-buttons .button:not(.add_media) {
		display: none;
	}

	input.button.ed_button {
		@include atXS {
			line-height: 1 !important;
			min-height: 30px;
		}
	}


	button.wp-switch-editor {
		box-sizing: content-box;
		border-color: rgba(var(--dpit-uac-main-menu), 0.6) !important;
		color: rgba(var(--dpit-uac-main-menu), 0.4) !important;
		background-color: transparent !important;
		border-bottom-width: 0 !important;
		font-weight: 500 !important;
		border-bottom: none !important;
		//border-radius: 2px;
	}

	.tmce-active button.switch-tmce,
	.html-active button.switch-html {
		//border-color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;
		//background-color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;
		background-color: #f6f7f7 !important;
		color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;
		border-bottom: 1px #f6f7f7 solid !important;


	}

	.html-active button.switch-html {
		border-bottom-right-radius: 2px !important;
	}

	button.insert-media {
		margin-bottom: 10px !important;
		top: 0 !important;
		margin-top: 0px !important;
	}

	button.wp-switch-editor {

		text-transform: uppercase;
		font-weight: 700;
		color: rgb(var(--dpit-uac-sec));
		border: 1px solid rgb(var(--dpit-uac-sec-border));
		background-color: transparent;
		@include transitionMultiFast(background-color, color);
		display: block;
		border-bottom-color: transparent;

		&:not(:hover) {
			color: rgba(var(--dpit-uac-sec), 0.7);
		}
	}

	.dpit-modal__title {
		padding-bottom: 17px;
	}

	&__content {

		textarea.wp-editor-area {
			background-color: rgb(var(--dpit-uac-main-menu));
			max-height: calc(80vh - 150px);

			@include atXS {
				max-height: calc(80vh - 200px);
			}

			@include atXXXS {
				max-height: calc(80vh - 300px);
			}
		}

		#dp-tmce-editor_ifr {
			max-height: calc(80vh - 200px);

			@include atXXXS {
				max-height: calc(80vh - 300px);
			}
		}

		textarea.wp-editor-area,
		#dp-tmce-editor_ifr {

			overflow-y: auto;

			@include atHoverAbleDev {

				scrollbar-width: thin;
				scrollbar-color: rgba(var(--dpit-uac-main-menu-bg), 0.5) rgba(var(--dpit-uac-main-menu-bg), 0.1);

				&::-webkit-scrollbar {
					width: unit(20);
				}

				&::-webkit-scrollbar-track {
					border-radius: 2px;

					background-color: rgba(var(--dpit-uac-main-menu-bg), 0.1);
				}

				&::-webkit-scrollbar-thumb {
					border-radius: 2px;
					background-color: rgba(var(--dpit-uac-main-menu-bg), 0.5);
				}

			}

		}


	}


}

:root.mce-fullscreen #dp-tmce-editor_ifr {
	height: 100vh !important;
}


.mce-btn.mce-active {
	box-shadow: none !important;
	border-color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;

	button {
		background-color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;

		i {
			color: rgb(var(--dpit-uac-menu-a-item)) !important;
			border-bottom-color: rgb(var(--dpit-uac-menu-a-item)) !important;
		}
	}

	&.mce-opened {
		button {
			background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.9) !important;
			color: rgb(var(--dpit-uac-menu-a-item)) !important;
		}
	}
}

.mce-menu .mce-menu-item.mce-active,
.mce-menu .mce-menu-item:hover {
	background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.9) !important;
	color: rgb(var(--dpit-uac-menu-a-item)) !important;
}

body:not(.mce-fullscreen) .dpit-mce-dialog {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	@include atS {
		top: 0;
		left: 0;
		transform: unset;
		right: 0;
		bottom: 0;
		box-shadow: unset;
		width: 100vw;
	}


}

body.mce-fullscreen {
	position: relative;

	.mce-tinymce.mce-fullscreen {
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;

		.mce-container-body {
			height: 100%;
			width: 100%;
		}
	}
}

.mce-container.mce-panel.mce-menu {
	@include z-index(7, true);
}

.mce-notification,
div.mce-inline-toolbar-grp,
.mce-container.mce-floatpanel {
	@include z-index(7, true);
}

.media-modal.wp-core-ui {
	@include z-index(8, true);
}

body.dpit-no-scroll {

	#mce-modal-block,
	#wp-link-backdrop {
		@include z-index(6, true);
	}

	#wp-link-wrap {

		.link-target {
			display: none !important;
		}

		@include z-index(7, true);
	}
}