.mce-container {
	display: none;
}

#dp_wp-link {
	position: relative;
	height: 100%;
}






body.dpit-no-scroll {
	#dp_wp-link-wrap.dp_wp-link-wrap #dp_most-recent-results {
		overscroll-behavior: contain;
	}
}


#dp_wp-link-wrap.dp_wp-link-wrap {
	height: 500px;
	display: block !important;
	min-width: 500px;
	padding: 15px 10px 10px;
	position: fixed;
	top: 50%;
	left: 50%;
	font-size: 13px;
	line-height: 1.4em;
	margin-top: 0 !important;
	margin-left: 0 !important;
	transform: translate(-50%, -50%) !important;

	#dpit-url-var-btn {
		margin-bottom: 10px;
	}

	.dpit-info-icon {
		pointer-events: none;
	}

	&.dpit-active {
		.dpit-info-icon {
			pointer-events: all;
		}
	}

	@include atS {
		top: 5vh;
		border-radius: 0;
		left: 0;
		transform: unset !important;
		right: 0;
		bottom: 0;
		box-shadow: unset !important;
		max-width: 100vw;
		min-width: 100vw !important;
		max-height: 90vh;
	}

	@include atXS {
		top: 0;
		height: calc(100vh - 25px);
	}



	@media screen and (max-height: 520px) {
		overflow: auto;
		height: 500px;
	}



	#link-options {
		position: relative;
		padding: 0 0 8px;
	}

	#dpwplink-url-vars-hint {
		position: absolute;
		top: 50%;
		left: 50%;
		border-radius: 2px;
		overflow: hidden;

		.dpwplink-url-vars-hint-item {
			display: block;
			padding: 5px 10px;

			&:hover {
				//background-color: rgba(var(--dpu-color-1), 0.05);
			}
		}
	}

	.dpwplink-custom-url-vars-hint {
		display: none;
	}

	.dpwplink-url-vars-label {

		display: flex;
		padding-top: 10px;


		&>span {
			font-size: 15px;
			font-weight: 600;
			text-transform: uppercase;
		}
	}



	#dp_link-selector {
		padding: 0 16px;
		position: absolute;
		top: 37px;
		left: 0;
		right: 0;
		bottom: 54px;
	}

	#dpwplink-url-vars-wrap {
		padding-bottom: 10px;
	}

	.dpwplink-url-vars {
		display: flex;


		margin-left: -5px;
		margin-right: -5px;
		margin-top: 5px;


		padding: 5px 10px 10px;

		&--system {
			max-width: 100%;
			overflow-x: auto;
			flex-shrink: 0;
			cursor: pointer;
		}

		&--custom {
			max-height: 150px;
			overflow-y: auto;
			flex-wrap: wrap;
		}

		&.dpit-scrollbar {
			@include atHoverAbleDev {
				scrollbar-width: thin;
				scrollbar-color: rgba(var(--dpit-uac-main-menu), 0.9) rgba(var(--dpit-uac-main-menu-bg), 0.2);

				&::-webkit-scrollbar-thumb {
					background-color: rgba(var(--dpit-uac-main-menu), 0.9);
					border: 1px solid rgba(var(--dpit-uac-main-menu), 0.8);
				}

				&::-webkit-scrollbar-track {
					background-color: rgba(var(--dpit-uac-main-menu-bg, 0.2));
					border: 1px solid rgba(var(--dpit-uac-main-menu), 0.5);
				}
			}
		}

		&__item {
			opacity: 0.9;
			box-sizing: content-box;
			@include transitionMultiFast(opacity, transform);
			//background-color: rgba(var(--dpu-color-1), 0.1);
			border: 1px solid rgb(var(--dpit-uac-main-menu));
			padding: 7px;
			border-radius: 2px;
			align-items: center;
			width: 44%;
			margin: 5px;

			&:not(.dpwplink-url-vars__item--system) {

				&:nth-child(4n+0) {
					background-color: rgba(var(--dpu-color-2), 0.8);
					color: rgba(var(--dpu-color-2-text), 1);
				}

				&:nth-child(4n+1) {
					background-color: rgba(var(--dpu-color-3), 0.8);
					color: rgba(var(--dpu-color-3-text), 1);
				}

				&:nth-child(4n+2) {
					background-color: rgba(var(--dpu-color-4), 0.8);
					color: rgba(var(--dpu-color-4-text), 1);
				}

				&:nth-child(4n+3) {
					background-color: rgba(var(--dpu-color-1), 0.8);
					color: rgba(var(--dpu-color-1-text), 1);
				}
			}

			&:hover {
				opacity: 1;
			}




			@include atXXS {
				width: 100%;
			}

			span {
				display: block;
				width: 100%;
				margin-bottom: 5px;
				word-wrap: break-word;
				margin-right: 5px;
				font-weight: 600;
			}

			input {
				width: calc(100% - 7px);

				@include atXS {
					border: 1px solid rgb(118, 118, 118) !important;
				}
			}

			&--id-only {
				width: auto;
				padding: 8px 10px;

				&:hover {
					transform: scale(1.01);
				}

				span {
					margin-bottom: 0px;
				}

				input {
					display: none;
				}
			}

			&--system {
				background-color: rgba(var(--dpit-uac-main-menu-bg), 1);
				color: rgb(var(--dpit-uac-main-menu));
				border: 2px solid rgb(var(--dpit-uac-main-menu));
				padding: 5px 7px;


				span {
					white-space: nowrap;
				}
			}
		}
	}


	#dp_wp-link {
		border: none !important;
		outline: none !important;
	}

	.link-target,
	.wp-link-text-field {
		display: none !important;
	}

	li {
		clear: both;
		margin-bottom: 0;
		border-bottom: 1px solid #f0f0f1;
		color: #2c3338;
		padding: 4px 6px 4px 10px;
		cursor: pointer;
		position: relative;
	}

	.item-title {
		display: inline-block;
		width: 80%;
		width: calc(100% - 68px);
		word-wrap: break-word;
	}

	.item-info {
		text-transform: uppercase;
		color: #646970;
		font-size: 11px;
		position: absolute;
		right: 5px;
		top: 5px;
	}

	#dp_most-recent-results,
	#dp_search-results {
		top: 140px;
		max-height: 250px;

		@include atHoverAbleDev {

			scrollbar-width: thin;
			scrollbar-color: rgba(var(--dpit-uac-main-menu-bg), 0.5) rgba(var(--dpit-uac-main-menu-bg), 0.1);

			&::-webkit-scrollbar {
				width: unit(20);
			}

			&::-webkit-scrollbar-track {
				border-radius: 2px;

				background-color: rgba(var(--dpit-uac-main-menu-bg), 0.1);
			}

			&::-webkit-scrollbar-thumb {
				border-radius: 2px;
				background-color: rgba(var(--dpit-uac-main-menu-bg), 0.5);
			}

		}

		@include atS {
			max-height: unset !important;
			margin-bottom: 15px;
		}

	}

	.dpit-url-heading {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		.button {
			text-transform: uppercase;
			margin: 0 3px 3px 0;

			&.button-secondary {
				color: rgb(var(--dpit-uac-sec));
				border: rgb(var(--dpit-uac-sec-border));
			}
		}

		#dpit-url-var-back-btn {}
	}

	.dpit-how-to {
		color: rgb(var(--dpit-uac-main-menu));
		display: block;
		margin: 3px 0;
		font-size: 13px;
		line-height: 1.1;
		font-family: inherit;
	}

	#dp_most-recent-results,
	#dp_search-results {
		&:focus {
			border-color: rgba(var(--dpu-color-2), 0.7);
			box-shadow: 0 0 0 1px rgba(var(--dpu-color-2), 0.5);
			outline: none;
		}
	}

	.query-results {
		border: 1px #dcdcde solid;
		margin: 0 0 12px;
		background: #fff;
		overflow: auto;
		position: absolute;
		left: 16px;
		right: 16px;
		bottom: 0;

		em {
			font-style: normal;
		}
	}

	ol,
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	.river-waiting {
		display: none;
		padding: 10px 0;
	}


	.query-notice {

		.query-notice-hint,
		.query-notice-default {
			color: $text-color-dark;
			border-left: 4px solid rgba(var(--dpit-uac-badge-bg), 0.9) !important;
			display: block;
			padding: 6px;
		}
	}

	#search-panel,
	#link-options {
		label {
			span:not(.spinner) {
				color: #444;

				font-size: 13px;
				line-height: 1.4em;
				display: none !important;
			}

			input {
				padding: 0 8px;
				line-height: 2;
				min-height: 30px;
				box-shadow: 0 0 0 transparent;
				border-radius: 2px;
				border: 1px solid #7e8993;
				outline: none;
				background-color: #fff;
				color: #32373c;
				font-size: 14px;
				margin: 0 1px;
				min-width: 99%;

				&:focus {
					border-color: rgba(var(--dpu-color-2), 0.7);
					box-shadow: 0 0 0 1px rgba(var(--dpu-color-2), 0.5);
					outline: none;
				}
			}
		}
	}

	#search-panel {
		margin-top: 0;
	}

	.link-search-wrapper {
		margin-top: 0;

		.spinner {
			z-index: 100000002 !important;
			position: absolute;
			left: 50%;
			transform: translateX(-100%);
			top: 180px;
		}
	}

	li {
		.item-long-info {
			font-weight: 300;
			font-size: 11px;
		}

		&.dpit-special-link {
			font-weight: bold;
			background-color: #dfdfdf;

			&--hidden {
				display: none;
			}
		}

		&:hover {
			background: rgba(var(--dpu-color-2), 0.2);

		}

		&.selected {
			background: rgba(var(--dpu-color-2), 0.8);

			span {
				color: rgba(var(--dpu-color-2-text), 1);
			}

			.item-long-info {
				color: rgba(var(--dpu-color-2-text), 1);
			}
		}


	}

	.dpit-modal {
		&__title {
			padding: 4px 16px;
		}

		&__buttons {
			padding: 8px 16px;

			#dp_wp-link-update {
				.spinner {
					pointer-events: none;
				}

				.button {
					float: right;
				}
			}
		}

	}



	.dpwplink-inactive-el {
		display: none;
	}
}


.wp-admin #dp_wp-link-wrap.dp_wp-link-wrap {
	border-radius: 2px;

	.submitbox .button {
		border-radius: 2px !important;
	}
}





