		//pointer-events: none;
		//background-color: rgba(var(--dpit-uac-main-menu-bg), 0.97);
		background: linear-gradient(140deg, rgba(var(--dpit-uac-main-menu-bg), 0.97) 0, rgba(var(--dpit-uac-main-menu-bg), 0.97) calc(100% - 150px), rgba(255, 255, 255, 0.85) calc(100% - 85px), rgba(255, 255, 255, 1) 100%);
	}


	&.active {
		@include z-index(5, true);
	}

