	#dp_wp-link-update,
	.link-search-wrapper {
		.spinner {

			@include z-index(5, true);
			background: url(../../../../../../wp-admin/images/spinner.gif) no-repeat !important;
			background-size: 20px 20px !important;
			background-position: center !important;
			vertical-align: middle !important;

			position: absolute !important;
			left: 50% !important;
			transform: translateX(-50%) !important;
			top: 175px !important;
			filter: alpha(opacity=70) !important;
			width: 20px !important;
			height: 20px !important;
			display: inline-block !important;
			visibility: visible !important;
			opacity: 0 !important;
			transition: opacity $transition-time-fast ease-in-out !important;

			&.is-active {
				opacity: .7 !important;
			}
		}
	}

	#dp_wp-link-update {
		position: relative !important;


		#dp_wp-link-submit {
			@include transitionMultiFast(background-color);

			&.spinner-bg {
				opacity: 0 !important;
			}
		}

		.spinner {

			top: 50% !important;
			left: unset !important;
			right: 0% !important;
			transform: translate(-30%, -50%) !important;
			background: url(../../../../../../wp-admin/images/spinner-2x.gif) no-repeat !important;
			background-size: 40px 40px !important;
			width: 40px !important;
			height: 40px !important;
		}

	}
}