.dpit-info-icon {
	width: 15px;
	height: 15px;
	margin-left: 5px;
	margin-bottom: 5px;
	flex-shrink: 0;
	cursor: help;

	pointer-events: all;
	color: rgb(var(--dpit-uac-menu-a-item-bg));
	@include transitionFast();

	&--inline {
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 0px;
	}

	&--bigger {
		width: 17px;
		height: 17px;
	}

	&--pre {
		margin-left: 0px;
		margin-right: 5px;
		margin-bottom: 3px;
	}


	svg {
		path {
			@include transitionFaster(fill);
			fill: rgba($text-color-darker, 0.8);
		}
	}

	&:hover {
		svg {
			path {
				fill: rgb(var(--dpit-uac-main-menu-bg));
			}
		}
	}


	&--modal {
		svg {
			path {
				fill: rgb(var(--dpit-uac-main-menu));
			}
		}

		&:hover {
			svg {
				path {
					fill: rgb(var(--dpit-uac-main-menu));

				}
			}
		}
	}

	&--builder-step-edit {

		&:hover {
			svg {
				path {
					fill: $text-color-darker;
				}
			}
		}
	}

	&--pro-only {
		svg {
			path {
				opacity: 0.5;
			}
		}
	}

	&--interaction,
	&--in-table-header {

		svg {
			path {
				fill: rgba(var(--dpit-uac-prim-bg), 0.9);
			}
		}


		&:hover {
			svg {
				path {
					fill: rgb(var(--dpit-uac-prim-bg));
				}
			}
		}
	}

	&--in-table-header {
		position: absolute;
		top: 7px;
		right: 10px;
	}
}